@media only screen and (max-width: 599px) {
  #hero {
    .hero-img {
      margin-bottom: -70%;
    }
    .heroBox {
      position: relative;
      top: unset;
      left: unset;
      width: 100%;
      padding: 0 4% 4rem 4%;
      text-align: center;
      margin: 0 auto;

      .heroBox-new {
        display: none;
      } // heroBox-new
      h1 {
        font-size: 4.4rem;
      }
      h3 {
        font-size: 2rem;
        font-weight: 400;
      }
      h6 {
        font-size: 1.8rem;
        padding-right: 0;
      }
    } // heroBox
  } // hero
  #info {
    .infoImg-left {
      width: 15%;
      float: left;
      z-index: 4;
      margin-top: -3rem;
    }
    .info-text {
      position: relative;
      top: unset;
      left: unset;
      text-align: center;
      margin: 6rem auto 0 auto;
      width: 70%;
      padding: 0;

      h2 {
        margin-top: 0;
        font-size: 4rem;
      }
      h6 {
        font-size: 1.8rem;
      }
    }
    .infoImg-right {
      float: right;
      width: 50%;
      margin-top: -20rem;
      max-height: 380px;
    }
  } // info
  .ingredients {
  .ingredients-box {
    padding-bottom: 50px;
    // background: linear-gradient(-53deg, $q10purpleLT 50%, $q10purple 50%);

    h2 {
      margin-bottom: 30px;
    }
  } // ingredients-box
  #ingredientsDesc {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 20px;

    .ingredientImg {
      float: none;
      margin-left: auto;
      margin-right: auto;
    }
    .ingredient-text {
      text-align: center;
      padding-top: 0;
      width: 90%;

      h5 {
        font-size: 20px;
      }
    } // ingredient-text
    .slick-dots {
      bottom: -4px;

      li {
        width: 14px;
        height: 14px;

        button:before {
          font-size: 30px; // good size for server
          // line-height: 16px;
        } // button:before
      } // li
    } // slick-dots
  } // ingredientsDesc
} //ingredients

} // Mobile - everything smaller than 600
