@media only screen and (min-width: 600px) and (max-width: 1024px) {
  .content-wrapper {
    .button-cta {
      font-size: 1.6rem;
      padding: .6rem 2rem;
    }
  }
  #hero {
    padding-bottom: 4.4rem;

    .heroBox {
      top: 1%;
      padding-left: 4%;

      .heroBox-new {
        margin-left: -0.8rem;
        margin-top: 0.6rem;
      }
      h1 {
        font-size: 4.6rem;
        margin-bottom: .6rem;
      }
      h3 {
        font-size: 2.2rem;
        margin-bottom: 1rem;
      }
      h6 {
        font-size: 1.8rem;
        line-height: 1.2;
        padding-right: 0;
        margin-top: 1.2rem;
        margin-bottom: 1.4rem;
      }
    } // heroBox
  } //hero
  #info {
    .info-text {
      padding-left: 20%;
      padding-right: 20%;

      h2 {
        margin-top: 0;
      }
    }
    .infoImg-right {
      width: 80%;
      margin-top: 9rem;
      margin-right: -6rem;
    }
  } // info
} // min 600 to max 1024 - Tablet
